/* Home Page - Luxury Circular Frame Design */
.home {
  font-family: var(--font-body);
  color: var(--text-dark);
  overflow-x: hidden;
}

/* Hero Section with Circular Frame */
.hero {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: linear-gradient(135deg, #faf8f5 0%, #f5f0e8 100%);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.texture-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 80%, rgba(212, 165, 116, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(212, 175, 55, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 182, 193, 0.05) 0%, transparent 50%);
  z-index: 2;
}

/* Hero Content */
.hero-content {
  position: relative;
  z-index: 3;
  text-align: center;
  max-width: 1000px;
  padding: 0 30px;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

/* Couple Names */
.name-abhishek,
.name-richa {
  font-family: var(--font-script);
  font-size: 4.5rem;
  color: var(--dusty-rose);
  margin: 0;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  line-height: 1.1;
  font-weight: 400;
  letter-spacing: 2px;
}

/* Circular Frame */
.circular-frame {
  position: relative;
  width: 320px;
  height: 320px;
  margin: 20px 0;
}

.frame-border {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  padding: 8px;
  background: linear-gradient(135deg, var(--gold) 0%, var(--dusty-rose) 50%, var(--gold) 100%);
  box-shadow: 
    0 20px 40px rgba(212, 165, 116, 0.3),
    0 8px 16px rgba(0, 0, 0, 0.1),
    inset 0 2px 4px rgba(255, 255, 255, 0.3);
  position: relative;
  overflow: hidden;
}

.frame-border::before {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.7) 100%);
  z-index: 1;
}

.couple-photo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  position: relative;
  z-index: 2;
  filter: brightness(1.05) contrast(1.1) saturate(1.1);
  transition: transform 0.6s ease;
}

.circular-frame:hover .couple-photo {
  transform: scale(1.02);
}

/* Wedding Announcement */
.wedding-announcement {
  text-align: center;
  margin: 20px 0;
}

.announcement-text {
  font-family: var(--font-heading);
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--charcoal);
  letter-spacing: 3px;
  margin-bottom: 15px;
  text-transform: uppercase;
  line-height: 1.2;
}

.wedding-date {
  font-family: var(--font-heading);
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--dusty-rose);
  letter-spacing: 2px;
  margin: 0;
  text-transform: uppercase;
  line-height: 1.2;
}

/* Explore Button */
.explore-button {
  background: linear-gradient(135deg, var(--dusty-rose) 0%, var(--gold) 100%);
  color: var(--white);
  border: none;
  padding: 18px 45px;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(212, 165, 116, 0.4);
  font-family: var(--font-body);
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-top: 20px;
}

.explore-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(212, 165, 116, 0.6);
  background: linear-gradient(135deg, var(--gold) 0%, var(--dusty-rose) 100%);
}

/* Countdown Section */
.countdown-section {
  padding: 100px 30px;
  background: linear-gradient(135deg, var(--white) 0%, var(--light-rose) 100%);
  text-align: center;
}

.countdown-container {
  max-width: 900px;
  margin: 0 auto;
}

.countdown-title {
  font-family: var(--font-script);
  font-size: 3.2rem;
  color: var(--dusty-rose);
  margin-bottom: 60px;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  font-weight: 400;
}

.countdown-timer {
  margin-bottom: 40px;
}

.wedding-day {
  font-family: var(--font-script);
  font-size: 2.8rem;
  color: var(--dusty-rose);
  font-weight: 400;
}

.countdown-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 40px;
  max-width: 700px;
  margin: 0 auto;
}

.countdown-item {
  text-align: center;
  padding: 30px 20px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 25px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border: 2px solid rgba(212, 165, 116, 0.2);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.countdown-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.countdown-number {
  font-family: var(--font-heading);
  font-size: 3.5rem;
  color: var(--charcoal);
  margin-bottom: 15px;
  font-weight: 700;
  line-height: 1;
}

.countdown-label {
  font-family: var(--font-body);
  font-size: 1rem;
  color: var(--text-light);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* Quick Info Section */
.quick-info-section {
  padding: 100px 30px;
  background: linear-gradient(135deg, var(--ivory) 0%, var(--light-rose) 100%);
}

.info-cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 50px;
  max-width: 1200px;
  margin: 0 auto;
}

.info-card {
  background: rgba(255, 255, 255, 0.95);
  padding: 50px 40px;
  border-radius: 30px;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border: 2px solid rgba(212, 165, 116, 0.2);
  transition: all 0.3s ease;
  backdrop-filter: blur(15px);
}

.info-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.card-icon {
  font-size: 3.5rem;
  margin-bottom: 25px;
  display: block;
}

.info-card h3 {
  font-family: var(--font-heading);
  font-size: 1.8rem;
  color: var(--charcoal);
  margin-bottom: 20px;
  font-weight: 600;
}

.info-card p {
  font-size: 1.125rem;
  color: var(--text-light);
  margin-bottom: 10px;
  line-height: 1.7;
  font-weight: 400;
}

.card-detail {
  font-size: 1rem;
  color: var(--dusty-rose);
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-content {
    padding: 0 20px;
  }
  
  .name-abhishek,
  .name-richa {
    font-size: 3.5rem;
  }
  
  .circular-frame {
    width: 280px;
    height: 280px;
  }
  
  .announcement-text {
    font-size: 1.5rem;
    letter-spacing: 2px;
  }
  
  .wedding-date {
    font-size: 1.8rem;
  }
  
  .countdown-title {
    font-size: 2.5rem;
  }
  
  .countdown-number {
    font-size: 2.8rem;
  }
  
  .countdown-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;
  }
  
  .info-cards-container {
    grid-template-columns: 1fr;
    gap: 35px;
  }
  
  .info-card {
    padding: 40px 30px;
  }
}

@media (max-width: 480px) {
  .hero {
    height: 100vh;
  }
  
  .main-content {
    gap: 30px;
  }
  
  .name-abhishek,
  .name-richa {
    font-size: 2.8rem;
  }
  
  .circular-frame {
    width: 240px;
    height: 240px;
  }
  
  .announcement-text {
    font-size: 1.2rem;
    letter-spacing: 1px;
  }
  
  .wedding-date {
    font-size: 1.5rem;
  }
  
  .explore-button {
    padding: 15px 35px;
    font-size: 1rem;
  }
  
  .countdown-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .countdown-item {
    padding: 25px 15px;
  }
  
  .countdown-number {
    font-size: 2.5rem;
  }
}
